var React = require('react');
var IntlMixin = require('react-intl').IntlMixin;
var RouteHandler = require('react-router').RouteHandler;

var App = React.createClass({

    mixins: [IntlMixin],

    render: function() {
        return (
            <div>this.getIntlMessage('dashboard')
                <RouteHandler/>
            </div>
        );
    }
});

module.exports = App;


