var React = require('react');
var Route = require('react-router').Route;

module.exports = (
    <Route handler={require('./components/App')}>
        <Route name="dashboard" path="/:locale/dashboard"
               handler={require('./components/Dashboard/Dashboard')}/>
    </Route>
);


